export default {
  name: "About",
  transition: "page",
  head () {
    return {
      title: 'Kat Lightfoot - About',
    }
  },
  async asyncData({ $prismic, error }) {
    try {
      const document = (await $prismic.api.getSingle('about')).data

    return {
      document
    };
  } catch (e) {
    error({ statusCode: 404, message: "Page not found" });
  }
}
}
